@import 'base/heading'; //
@import 'base/font-material';


/*  */
@import "components/sprite";
@mixin retina-sprites($retina-groups) {
	@each $retina-group in $retina-groups {
		$sprite-name: nth($retina-group, 1);
		.#{$sprite-name} {
			@include retina-sprite($retina-group);
		}	
	}
}
[class^='ico-'] {
	display: inline-block;
	vertical-align: middle;
}
//@include retina-sprites($retina-groups);


@import 'base/layout'; //
@import 'base/gnb'; //
@import 'base/footer'; //

@import 'components/visible'; // 


a {
	color: $link-color;
}
html {
	font-size: $html-font-size;
	@include breakpoint($sm){
		font-size: rem(18);
	}
}
body {
	overflow-x: hidden;
	color: $body-font-color;
	line-height: 1.5;
	font-family: $font-set;
	letter-spacing: -0.015rem;
	word-break: keep-all;
	font-weight: 400;
	@include breakpoint($sm) {
		word-break: keep-all;
	}
	&.ovh, 
	&.modal-open  {
		// position: relative;
		overflow: hidden;
		// height: 100vh;
	}
}

iframe {
	border: 0;
}

* {
	box-sizing: border-box;
}

button {
	padding: 0;
	border: 0;
	background-color: transparent;
	overflow: visible;
}

i {
	font-style: normal;
}
img {
	max-width: 100%;
}



.col{float: left;width: 100%;}
.row{margin-left:-10px;margin-right: -10px}
.row dl{/* clear: both; */}
.row .col{padding: 10px;}
.no-padding{margin-left:0;margin-right: 0}
.no-padding>.col{padding:0;}
.padding-type>.col{padding: 0 10px;}

@mixin gridGen($columns, $size: ''){
	@for $i from 1 through $columns {
		$width : 100% / $columns * $i;
		@if($size == 'desktop'){
			.l#{$i}{ width: $width; }
			.l-offset-#{$i}{ margin-left: $width; }
		} 
		@else if($size == 'tablet'){
			.m#{$i}{ width: $width; }
			.m-offset-#{$i}{ margin-left: $width; }
		} 
		@else if($size == 'mobile'){
			.s#{$i}{ width: $width; }
			.s-offset-#{$i}{ margin-left: $width; }
		} 
		@else if($size == 'smobile'){
			.x#{$i}{ width: $width; }
			.x-offset-#{$i}{ margin-left: $width; }
		} 
	}
}

@include gridGen(12, 'mobile');

@include breakpoint($md) {
	@include gridGen(12, 'tablet');
}
@media (max-width: 600px){
	@include gridGen(12, 'smobile');
}
@media (min-width: 1030px){
	@include gridGen(12, 'desktop');
}